.updated { padding: 10px 4px; }

.visitor-map-actions { border-width: 0; width: 99%; }

.visitor-map-key { padding: 2px; border-spacing: 2px; width: 99%; text-align: right; }

.visitor-map-key .key-ban { background-color: #008000; color: white; padding: 0px 4px; text-decoration: none; border-radius: 10px; }

.visitor-map-key .key-unban { background-color: #B30000; color: white; padding: 0px 4px; text-decoration: none; border-radius: 10px; }

.visitor-map-key .key-admin, .visitor-map-key .key-disabled { background-color: #D4D4D4; color: white; padding: 0px 4px; text-decoration: none; border-radius: 10px; cursor: pointer; }

.visitor-maps-data { border-width: 0; border-spacing: 2px; padding: 2px; width: 99%; }

.visitor-maps-data .referer .disabled { background-color: #D4D4D4; color: white; padding: 0px 4px; text-decoration: none; border-radius: 10px; cursor: pointer; }

.visitor-maps-data .referer-string { color: green; text-decoration: none; border-bottom: 1px dashed green; }

.visitor-maps-data .visitors-since { text-align: center; }

.visitor-maps-data .visitors-pagination { text-align: left; }

.visitor-maps-data .visitors { vertical-align: top; }

.visitor-maps-data .visitors .outer-table { border-width: 0; border-spacing: 0; padding: 2px; width: 99%; }

.visitor-maps-data .visitors .outer-table td { vertical-align: top; }

.visitor-maps-data .visitors .outer-table td .inner-table { border-spacing: 0; width: 100%; }

.visitor-maps-data .visitors .outer-table td .inner-table .table-top { color: black; background-color: #96C6F5; text-align: left; font-weight: bold; }

.visitor-maps-data .visitors .outer-table td .inner-table .column-light { color: black; background-color: white; white-space: nowrap; }

.visitor-maps-data .visitors .outer-table td .inner-table .column-dark { color: black; background-color: #F1F8FE; white-space: nowrap; }

.visitor-maps-data .visitors .outer-table td .inner-table .status { text-align: left; vertical-align: top; }

.visitor-maps-data .visitors .outer-table td .inner-table .ip-address .admin { background-color: #D4D4D4; color: white; padding: 0px 4px; text-decoration: none; border-radius: 10px; cursor: pointer; }

.visitor-maps-data .visitors .outer-table td .inner-table .ip-address .ban { background-color: #008000; color: white; padding: 0px 4px; text-decoration: none; border-radius: 10px; }

.visitor-maps-data .visitors .outer-table td .inner-table .ip-address .unban { background-color: #B30000; color: white; padding: 0px 4px; text-decoration: none; border-radius: 10px; }

.visitor-maps-data .visitor-stats { border-width: 0; border-spacing: 3px; padding: 0px; width: 600px; }

.visitor-maps-data br .clear { clear: both; }

.wo-map { margin-left: auto; margin-right: auto; border-width: 0; padding: 0; border-spacing: 0; }

